<template>
  <div class="game-container">
    <h2>–ù–∞–π–¥–∏ —Ñ–æ—Ç–æ –≥–¥–µ: <br> {{ targetText }}</h2>
    <div class="grid" :class="{ shuffling: isShuffling }">
      <div
          v-for="(card, index) in cards"
          :key="index"
          class="card"
          :class="{ flipped: card.flipped, disabled: isShuffling }"
          @click="flipCard(index)">
        <div class="card-inner">
          <div class="card-front">?</div>
          <div class="card-back">
            <img :src="card.image" alt="Card Image" class="card-image" />
          </div>
        </div>
      </div>
    </div>

    <transition name="fade">
      <div v-if="showModal" class="modal-overlay">
        <transition name="scale">
          <div :class="modalClass" class="modal small-modal">
            <div class="test">
              <p class="test-title">–¢—ã –≤—ã–±—Ä–∞–ª:</p>
              <img  :src="selectedImage" alt="–í—ã–±—Ä–∞–Ω–Ω–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ" class="modal-image" />
            </div>
            <div class="test">
              <p class="test-title">–ù—É–∂–Ω–æ –±—ã–ª–æ –Ω–∞–π—Ç–∏:</p>
              <img  :src="targetImage" alt="–¶–µ–ª–µ–≤–∞—è –∫–∞—Ä—Ç–∏–Ω–∫–∞" class="modal-image" />
            </div>
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue';

// –ú–∞—Å—Å–∏–≤ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π —Å –ø–æ–¥–ø–∏—Å—è–º–∏
const images = [
  { src: require('@/assets/vanya1.png'), text: '–ë–µ–Ω–∏–∫ —Å–º–æ—Ç—Ä–∏—Ç –ø–æ—Ä–Ω–æ –≤ –º–∞—Ä—à—Ä—É—Ç–∫–µ' },
  { src: require('@/assets/vanya2.png'), text: '–ë–µ–Ω–∏–∫ —Å–ø–∏—Ç —Å —á–ª–µ–Ω–æ–º –≤ –∂–æ–ø–µ' },
  { src: require('@/assets/vanya3.png'), text: '–ë–µ–Ω–∏–∫ –ø–ª–∞—á–µ—Ç –ø–æ—Å–ª–µ —Å–µ–∫—Å–∞ —Å –º—É–∂—á–∏–Ω–æ–π' },
  { src: require('@/assets/vanya4.png'), text: '–ë–µ–Ω–∏–∫ —Ä–∞—Å—Å–∫–∞–∑—ã–≤–∞–µ—Ç —Å–∫–æ–ª—å–∫–æ —Ä–∞–∑ –≤–∑—è–ª –≤ —Ä–æ—Ç (—Å –õ–µ–Ω–æ—Ä–æ–º –∏ –§–æ–º–æ–π)' },
  { src: require('@/assets/vanya5.png'), text: '–ë–µ–Ω–∏–∫ –∏–≥—Ä–∞–µ—Ç –≤ —Ä–æ–ª–µ–≤—ã–µ –∏–≥—Ä—ã (—Å –õ–µ–Ω–æ—Ä–æ–º –∏ –§–æ–º–æ–π)' },
  { src: require('@/assets/vanya6.png'), text: '–ë–µ–Ω–∏–∫ —Å–º–æ—Ç—Ä–∏—Ç –ø–æ—Ä–Ω–æ —Å –∏—à–∞–∫–∞–º–∏' },
  { src: require('@/assets/vanya7.png'), text: '–ë–µ–Ω–∏–∫ –Ω—é—Ö–∞–µ—Ç –∂–æ–ø—É –õ–µ–Ω–æ—Ä–∞' },
  { src: require('@/assets/vanya.png'), text: '–ë–µ–Ω–∏–∫ –ø–æ–¥ –ø–∏—Å–∞—é—â–∏–º —Ñ–æ–Ω—Ç–∞–Ω–æ–º' }
];

// –ü–µ—Ä–µ–º–µ—à–∞–Ω–Ω—ã–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
const shuffledImages = ref([...images].sort(() => Math.random() - 0.5));

// –¶–µ–ª–µ–≤–∞—è –∫–∞—Ä—Ç–∏–Ω–∫–∞ –∏ —Ç–µ–∫—Å—Ç
const target = computed(() => shuffledImages.value[Math.floor(Math.random() * shuffledImages.value.length)]);
const targetImage = computed(() => target.value.src);
const targetText = computed(() => target.value.text);

// –ö–∞—Ä—Ç–æ—á–∫–∏
const cards = ref(shuffledImages.value.map(image => ({ image: image.src, flipped: false })));

// –°–æ—Å—Ç–æ—è–Ω–∏—è –º–æ–¥–∞–ª–∫–∏ –∏ –∏–≥—Ä—ã
const showModal = ref(false);
const gameOver = ref(false);
const isShuffling = ref(false);
const selectedImage = ref('');
const isCorrect = ref(false);

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–µ–≤–æ—Ä–æ—Ç–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
const flipCard = (index: number) => {
  if (gameOver.value || cards.value[index].flipped || isShuffling.value) return;
  cards.value[index].flipped = true;
  gameOver.value = true;
  selectedImage.value = cards.value[index].image;

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ –≤—ã–±–æ—Ä–∞
  isCorrect.value = selectedImage.value === targetImage.value;

  showModal.value = true;

  setTimeout(() => {
    restartGame();
  }, 2000);
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞ –∏–≥—Ä—ã
const restartGame = () => {
  showModal.value = false;
  isShuffling.value = true;
  cards.value.forEach(card => card.flipped = false);

  setTimeout(() => {
    shuffledImages.value = [...images].sort(() => Math.random() - 0.5);
    cards.value = shuffledImages.value.map(image => ({ image: image.src, flipped: false }));
    gameOver.value = false;
    isShuffling.value = false;
    isCorrect.value = false; // –°–±—Ä–∞—Å—ã–≤–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ —É–≥–∞–¥–∞–ª/–Ω–µ —É–≥–∞–¥–∞–ª
  }, 500);
};

// –û—Ç–∫–ª—é—á–∞–µ–º –≤—ã–±–æ—Ä –∏ –ø—Ä–æ–∫—Ä—É—Ç–∫—É –ø—Ä–∏ —Å—Ç–∞—Ä—Ç–µ –∏–≥—Ä—ã
onMounted(() => {
  document.documentElement.style.userSelect = 'none';
  document.documentElement.style.touchAction = 'manipulation';
  document.documentElement.style.overflow = 'hidden';
});

// –ö–ª–∞—Å—Å –º–æ–¥–∞–ª–∫–∏ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ –≤—ã–±–æ—Ä–∞
const modalClass = computed(() => isCorrect.value ? 'modal-correct' : 'modal-incorrect');
</script>

<style scoped>

.test-img{
  height: 100%;
  width: auto;
}
.test-title{
height: 50px;
}
.test{
  height: 300px;
}

* {
  touch-action: manipulation;
  user-select: none;
}

.game-container {
  text-align: center;
  font-family: Arial, sans-serif;
  max-width: 100vw;
  overflow: hidden;
  padding: 10px;
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(130px, 1fr)); /* –°–¥–µ–ª–∞–Ω–æ –º–µ–Ω—å—à–µ */
  gap: 10px;
  justify-content: center;
  margin: 20px auto;
  max-width: 400px;
  transition: transform 1s ease-in-out;
}

.card {
  width: 100%;
  aspect-ratio: 1;
  perspective: 1000px;
  cursor: pointer;
  transition: transform 0.5s;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background: #fff;
}

.card.disabled {
  pointer-events: none;
}

.card-inner {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.card.flipped .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  border-radius: 10px;
}

.card-front {
  background: linear-gradient(135deg, #ff8c00, #ff4500);
  color: white;
}

.card-back {
  background: white;
  transform: rotateY(180deg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.modal-image {
  width: 70%;
  min-height: 100px;
  min-width: 100px;
  max-width: 250px;
  margin: 10px 0;
  border-radius: 10px;
}

.small-modal {
  display: flex;
  justify-content: space-between;
  width: 60%;
  max-width: 280px;
  padding: 15px;
  background: white;
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.modal-correct {
  background-color: #d4edda; /* –°–≤–µ—Ç–ª–æ-–∑–µ–ª–µ–Ω—ã–π */
}

.modal-incorrect {
  background-color: #f8d7da; /* –°–≤–µ—Ç–ª–æ-–∫—Ä–∞—Å–Ω—ã–π */
}
</style>
